                                    <td>
                                        获取元素的左边框宽度，等价于 border-left
                                    </td>
                                </tr>

                                <tr>
                                    <th>4</th>
                                    <td>clientTop</td>
                                    <td>box.clientTop;</td>
                                    <td>
                                        获取元素的上边框高度，等价于 border-top
                                    </td>
                                </tr>

                                <tr>
                                    <th>5</th>
                                    <td>getBoundingClientRect</td>
                                    <td>box.getBoundingClientRect();</td>
                                    <td>
                                        获取包含元素可视区域元素的对象，返回的示例对象包含的属性如下
                                        <ul>
                                            <li><pre>bottom: 270     border底部到视口顶部的距离</pre></li>
                                            <li><pre>top: 20         border顶部部到视口顶部的距离</pre></li>
