                                        调用<code>scroll</code>, <code>scrollBy</code>,<code>scrollTo</code>都一样
                                    </td>
                                </tr>

                                <tr>
                                    <td><pre>{{pres[0]}}</pre></td>
                                    <td>
                                        传入对象，指定滚动是否有过渡效果
                                    </td>
                                </tr>

                                <tr>
                                    <th rowspan="3">6</th>
                                    <td rowspan="3">scrollIntoView</td>
                                    <td>box.scrollIntoView();</td>
                                    <td>
                                        将元素滚动到可视区域
                                    </td>
                                    <td rowspan="2"></td>
                                </tr>

                                <tr>
                                    <td>box.scrollIntoView(false);</td>
                                    <td>
                                        底部对齐，true顶部对齐
                                    </td>
                                </tr>

                                <tr>
                                    <td><pre>{{pres[1]}}</pre></td>
                                    <td>
                                        传入对象
                                    </td>
                                    <td>
                                        对齐方式有如下几种
                                        <ul>
                                            <li>start：顶部对齐</li>
                                            <li>center：中间对齐</li>
                                            <li>end：底部对齐</li>
                                            <li>nearest：哪头最近对齐哪头</li>
                                        </ul>
                                    </td>
                                </tr>

                                <tr>
                                    <th>8</th>
                                    <td>onscroll</td>
                                    <td><pre>{{pres[2]}}</pre></td>
                                    <td>
