<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      rect: null,
      time: "2019-3-25",
      title: "自定义Html元素",
      items: [
        {
          title: "可视化大小",
          target: "#client"
        },
        { divider: true },
        {
          title: "滚动",
          target: "#scroll"
        }
      ],
      pres: 
[
`
box.scroll({
    top: 200,
    left: 0,
    behavior: 'smooth' // auto没有动画
});
`,
`p.scrollIntoView({
    behavior: "smooth",
    block: "nearest", // 垂直对齐方式
    inline: "nearest" // 水平对齐方式
});`,
`box.onscroll = e => {
    if(e.scrollHeight - e.scrollTop === e.clientHeight){
        console.log('元素滚动到底部了')
    }
}`,
`
    <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg">
        <rect width="70" height="50" fill="green"/>
    </svg>
`,
`
    const rect = document.querySelector('rect');
    rect.addEventListener('wheel', e => { 
        scale = 1 + (e.deltaY / 1000); // deltaY表示垂直滚动量
        rect.style.transform = \`scale(\${scale})\`;
    }, { 
        passive: true // 告诉浏览器我不会使用preventDefault从而使此事件执行的更加流畅
    }); 
`,
`
    rect{
        transition: transform .3s;
    }
`
]
    };
  },
    mounted() {
        this.rect = document.querySelector('rect');
        this.rect.addEventListener('wheel', this.wheelFn, { 
            passive: true
        }); 
    },
    methods: {
        wheelFn(e) {
            let scale = 1 + (e.deltaY / 800);
            this.rect.style.transform = `scale(${scale})`;
        }
    },
    destroyed() {
        this.rect.removeEventListener('wheel', this.wheelFn);
    },
};
</script>

